<template>
  <div>
    <mt-navbar v-model="selected">
      <mt-tab-item id="1">Date</mt-tab-item>
      <mt-tab-item id="2">Category</mt-tab-item>
    </mt-navbar>

    <!-- tab-container -->
    <mt-tab-container v-model="selected">
      <mt-tab-container-item id="1">
        <!-- <div style="height = 4px;">&nbsp;</div> -->
        <!--  -->
      <el-collapse accordion>
          <!-- 5-9 -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft">Day 1</div>
              <div class="TitleCenter">2018-05-09</div>
              <div class="TitleRight"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDay1" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>
          <div class="clear"></div>
          <!-- 5-10 -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft">Day 2</div>
              <div class="TitleCenter">2018-05-10</div>
              <div class="TitleRight"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDay2" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>
          <div class="clear"></div>
          <!-- 5-11 -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft">Day 3</div>
              <div class="TitleCenter">2018-05-11</div>
              <div class="TitleRight"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDay3" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>
          <div class="clear"></div>
          <!-- 5-12 -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft">Day 4</div>
              <div class="TitleCenter">2018-05-12</div>
              <div class="TitleRight"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDay4" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>
          <div class="clear"></div>
          <!-- 5-13 -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft">Day 5</div>
              <div class="TitleCenter">2018-05-13</div>
              <div class="TitleRight"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDay5" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>
          <div class="clear"></div>
          <!-- 5-14 -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft">Day 6</div>
              <div class="TitleCenter">2018-05-14</div>
              <div class="TitleRight"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDay6" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>
        </el-collapse>

      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <!--  -->
        <el-collapse accordion>
          <!--  -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft"></div>
            </template>

            <!--  -->
            <boxcard v-for="(item,index) in cardDataFilght" :key="index" :data="item">
            </boxcard>
                        <!--  -->
            <boxcard v-for="(item,index) in cardDataDayTransport" :key="index" :data="item">
            </boxcard>

          </el-collapse-item>
          <!--  -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDayHotel" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>

          <!--  -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDayScenery" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>

          <!--  -->
          <el-collapse-item>
            <template slot="title">
              <div class="TitleLeft"></div>
            </template>
            <!--  -->
            <boxcard v-for="(item,index) in cardDataDayFood" :key="index" :data="item">
            </boxcard>
          </el-collapse-item>

        </el-collapse>

      </mt-tab-container-item>
    </mt-tab-container>

  </div>
</template>

<script>
import boxcard from "@/components/box-card";
export default {
  data() {
    return {
      selected: "1",
      // 
      cardDataDay1: [
        {
          Type: "flightHeader",
          Title: {
            key: "",
            value: " NH968"
          },
          text: [
            { key: "", value: "01:45 ~ 05:40" },
            { key: "From", value: "T2" },
            { key: "To", value: "" },
            { key: "", value: "" }
          ],
          url:
            "https://m.ctrip.com/webapp/hybrid/schedule/detail.html?navBarStyle=gray&queryDate=2018-05-09&flightNo=NH968&queryType=1&fromurl=https%3A%2F%2Fm.ctrip.com%2Fwebapp%2Fhybrid%2Fschedule%2Fsearch.html"
        },
        {
          Type: "transportHeader",
          Title: {
            key: "",
            value: "JR- / <br>A-"
          },
          text: [
            { key: "", value: "590" },
            { value: "<b>JR- / " },
            { key: "From", value: "" },
            { key: "To", value: "" },
            {
              value:
                "<a href='http://www.haneda-tokyo-access.com/cn/sp/'>"
            },
            {
              value:
                "<a href='http://www.haneda-tokyo-access.com/cn/transport/international.html'> & "
            },
            {
              value:
                "<a href='http://www.haneda-tokyo-access.com/cn/transport/trainlinemap.html'>"
            },
            { value: "<b>A-" },
            { key: "From", value: "" },
            { key: "To", value: "" },
            {
              value:
                "<a href='http://www.tokyometro.jp/lang_en/station/rosen_cs_1803.pdf'>"
            }
          ],
          url: false
        },
        {
          Type: "hotelHeader",
          Title: {
            key: "",
            value: ""
          },
          text: [
            {
              key: "",
              value:
                "<a href='http://google.com/maps/place/2+Chome-4-5+Nihonbashibakuroch%C5%8D%2B%E4%B8%AD%E5%A4%AE%E5%8C%BA%2C+%E4%B8%9C%E4%BA%AC%E5%B8%82+103-0002%2B%E6%97%A5%E6%9C%AC'>2 Chome-4-5 Nihonbashibakuroch, ,  103-0002, "
            },
            { key: "", value: "Torinoth" },
            {
              key: "",
              value: "<a href='tel:+81-70-1254-0906'>+81-70-1254-0906"
            },
            {
              key: "",
              value: "<a href='tel:+81-90-4436-0906'>+81-90-4436-0906"
            },
            { key: "", value: "" },
            { value: "5912:0016:00" },
            { value: "51420:0011:00" },
            { value: "" },
            { value: "" },
            {
              value: ""
            }
          ],
          url: false
        },
        {
          Type: "sceneryHeader",
          Title: {
            key: "",
            value: "Tokyo Skytree"
          },
          text: [
            { key: "", value: "<b>A-" },
            { key: "To", value: "" },
            { key: "", value: "8:00-22:0021:00" },
            { key: "", value: "2820" },
            {
              key: "",
              value:
                "<a href='https://m.mafengwo.cn/poi/11977.html'>Tokyo Skytree"
            },
            {
              value:
                "<a href='https://m.ctrip.com/html5/you/sight/dongjing294/109876.html#ctm_ref=www_hp_bs_lst'>"
            }
          ],
          url: false
        },
        {
          Type: "sceneryHeader",
          Title: {
            key: "",
            value: ""
          },
          text: [
            { key: "", value: "<b>H-" },
            { key: "To", value: "" },
            { key: "", value: "" },
            {
              key: "",
              value:
                "<a href='https://m.mafengwo.cn/poi/6777538.html'>Meguro Gawa"
            }
          ],
          url: false
        }
      ],
      cardDataDay2: [
        {
          Type: "sceneryHeader",
          Title: { key: "", value: "--" },
          text: [
            {
              key: "Strava",
              value:
                "<a href='https://www.strava.com/routes/12613154'>--"
            },
            { key: "", value: "116.2KM" },
            {
              key: "",
              value:
                "<a href='https://m.mafengwo.cn/poi/6929887.html'>"
            },
            {
              value:
                "<a href='https://m.mafengwo.cn/poi/18548704.html'>"
            }
          ],
          url: false
        },
        {
          Type: "sceneryHeader",
          Title: {
            key: "",
            value: ""
          },
          text: [
            { key: "", value: "<b>" },
            {
              key: "To",
              value: "<a href='https://m.mafengwo.cn/poi/12384.html'>"
            },
            {
              key: "",
              value:
                "<a href='https://m.mafengwo.cn/poi/7390777.html'><b>DiverCity Tokyo"
            },
            {
              value:
                "<a href='https://m.mafengwo.cn/poi/13237.html'><b>"
            },
            {
              value:
                "<a href='https://m.mafengwo.cn/poi/13386.html'><b>"
            }
          ],
          url: false
        }
      ],
      cardDataDay3: [
        {
          Type: "sceneryHeader",
          Title: { key: "", value: "Mount Fuji" },
          text: [{ key: "", value: "" }],
          url: false
        }
      ],
      cardDataDay4: [
        {
          Type: "sceneryHeader",
          Title: { key: "", value: "" },
          text: [{ key: "", value: "" }],
          url: false
        }
      ],
      cardDataDay5: [],
      cardDataDay6: [
        {
          Type: "flightHeader",
          Title: {
            key: "",
            value: " NH967"
          },
          text: [
            { key: "", value: "22:30 ~ 00:35" },
            { key: "From", value: "" },
            { key: "To", value: "T2" },
            { key: "", value: "" }
          ],
          url:
            "https://m.ctrip.com/webapp/hybrid/schedule/detail.html?navBarStyle=gray&queryDate=2018-05-14&flightNo=nh967&queryType=1&fromurl=https%3A%2F%2Fm.ctrip.com%2Fwebapp%2Fhybrid%2Fschedule%2Fsearch.html"
        }
      ]
    };
  },
  computed: {
    AllCardInfo() {
      return this.cardDataDay1.concat(
        this.cardDataDay2,
        this.cardDataDay3,
        this.cardDataDay4,
        this.cardDataDay5,
        this.cardDataDay6
      );
    },
    cardDataFilght() {
      return this.AllCardInfo.filter(item => {
        return item.Type == "flightHeader";
      });
    },
    cardDataDayTransport() {
      return this.AllCardInfo.filter(item => {
        return item.Type == "transportHeader";
      });
    },
    cardDataDayHotel() {
      return this.AllCardInfo.filter(item => {
        return item.Type == "hotelHeader";
      });
    },
    cardDataDayFood() {
      return this.AllCardInfo.filter(item => {
        return item.Type == "foodHeader";
      });
    },
    cardDataDayScenery() {
      return this.AllCardInfo.filter(item => {
        return item.Type == "sceneryHeader";
      });
    }
  },
  components: {
    boxcard
  },
  created() {
    document.getElementsByTagName("title")[0].innerText =
      "2018";
  },
  methods: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.mint-header {
  background-color: #26a2ff !important;
  height: 45px !important;
  font-size: 18px;
}

.mint-navbar {
  margin-bottom: 5px !important;
}

.mint-tab-item-label {
  font-size: 18px !important;
  /* font-weight: bold; */
}

.el-collapse-item__header__arrow.el-icon-arrow-right {
  float: left;
  line-height: 44px;
  color: white;
}

.el-collapse-item__header {
  display: flex;
  background-color: #26a2ff !important;
}

.TitleLeft {
  flex: 1;
}

.TitleCenter {
  flex: 1;
  text-align: center;
}

.TitleRight {
  flex: 1;
  margin-right: 15px;
  text-align: right;
}

.TitleLeft,
.TitleCenter,
.TitleRight {
  font-size: 16px;
  color: white;
}

.ItemBox {
  border: #cdcdcd solid 1px;
  /* background: white; */
  border-radius: 10px;
  font-size: 14px;
}

.clear {
  clear: both;
}

.el-card {
  margin: 15px 0px !important;
}

.el-card__header {
  padding: 10px 20px !important;
}

.flightHeader .el-card__header {
  background: #4062e0;
}

.transportHeader .el-card__header {
  background: #64c819;
}

.hotelHeader .el-card__header {
  background: #ee842c;
}

.foodHeader .el-card__header {
  background: #9f4fe9;
}

.sceneryHeader .el-card__header {
  background: #e14da4;
}

.el-collapse-item__content {
  padding: 0px 15px !important;
}

.el-collapse-item__wrap {
  background-color: #cdcdcd !important;
}

.el-card .span {
  line-height: 10px;
}

.flexboxtitle {
  display: flex;
  color: white;
  font-size: 16px;
}

.Titleflex1 {
  flex: 1;
  width: 25px;
}

.Titleflex2 {
  flex: 4;
  padding-left: 10px;
}

.flexbox {
  display: flex;
  font-size: 14px;
  margin: 5px;
}

.flex1 {
  flex: 1;
}

.flex2 {
  flex: 3;
  padding-left: 10px;
}
</style>



